import React from 'react';

export default function Card({title, text, imageURL}) {
    return (
        <div className="card m-3 bg-dark" style={{ width: '18rem' }}>
            <div className="card-body text-light">
                <h5 className="card-title">{title}</h5>
                <p className="card-text"> {text}</p>
            </div>
            <img className="card-img-bottom m-1 p-1" src={imageURL} alt="Book Image" style={{ width: 'auto', height: 'auto', maxHeight: '300px', objectFit: 'contain',}} />
        </div>
    );
}