import React from "react";
import Navbar from "./Navbar";
import AboutMe from "./AboutMe";
import "./Main.css";

export default function Main() {
    return (
        <div className="container-fluid p-0 ">
            <div className="row justify-content-end g-0">
                <div className= "col-10 p-0 vh-100 main">
                    <nav className="row justify-content-start g-0">
                        <header className="col main d-flex flex-column justify-content-end align-items-start">
                            <h1 className = "pagetitle">
                                About Me
                                <hr className="underline"/>
                            </h1>
                            
                        </header>
                        <Navbar />
                    </nav>
                    <AboutMe/>
                </div>
            </div>
        </div>
    );
}